import React, { useState, useEffect, useRef, useCallback } from 'react';
import { delay, propTypeValidation } from './utils';
import { DialogBoxProps } from '../../../types';
import './DialogBox.css';

const DialogBox: React.FC<DialogBoxProps> = ({
  text: propText,
  loop,
  hideCursorAfterText,
  typeSpeed,
  onComplete,
  textStyle,
}) => {
  const [text, setText] = useState('');
  const [blink, setBlink] = useState(false);
  const [hideCursor, setHideCursor] = useState(true);
  const typeSpeedDelayRef = useRef<any>(null);
  const loopingRef = useRef<boolean>(false);
  const audioPlayingRef = useRef<boolean>(false);

  const animateText = useCallback(async () => {
    const runAnimation = async (str: string) => {
      const textArr = typeof str == 'string' && str.trim().split('');
      if (textArr) {
        setBlink(false);
        let currentText = '';
        const typeSpeedDelay = new delay(typeSpeed || 15);
        typeSpeedDelayRef.current = typeSpeedDelay;

        for (let char = 0; char < textArr.length; char++) {
          try {
            await typeSpeedDelay.getPromise();
            if (!loopingRef.current && loop !== true) {
              // Animation was cancelled
              return;
            }
            // Safely play audio with promise handling
            const audioElement = document.querySelector('#typingSound') as HTMLAudioElement | null;
            if (audioElement && !audioPlayingRef.current) {
              audioPlayingRef.current = true;
              audioElement.play().catch(() => {
                // Ignore play errors (e.g., interrupted by pause)
                audioPlayingRef.current = false;
              });
            }
            currentText += textArr[char];
            setText(currentText);
          } catch (error) {
            // Animation was cancelled
            return;
          }
        }
        // Safely pause audio
        const audioElement = document.querySelector('#typingSound') as HTMLAudioElement | null;
        if (audioElement && audioPlayingRef.current) {
          audioElement.pause();
          audioPlayingRef.current = false;
        }
        setBlink(true);
        // Call onComplete callback when typing finishes
        if (onComplete) {
          onComplete();
        }
      }
    };

    try {
      const shouldLoop = loop || false;
      setHideCursor(false);
      loopingRef.current = true;

      do {
        await runAnimation(propText);
      } while (shouldLoop && loopingRef.current);

      if (hideCursorAfterText) {
        setHideCursor(true);
      }
    } catch (error) {}
  }, [propText, loop, hideCursorAfterText, typeSpeed, onComplete]);

  useEffect(() => {
    // Reset state when text changes
    setText('');
    setBlink(false);

    // Cancel any ongoing animation
    if (typeSpeedDelayRef.current) {
      typeSpeedDelayRef.current.cancel();
    }
    loopingRef.current = false;

    // Stop audio if playing
    const audioElement = document.querySelector('#typingSound') as HTMLAudioElement | null;
    if (audioElement && audioPlayingRef.current) {
      audioElement.pause();
      audioPlayingRef.current = false;
    }

    // Start new animation
    if (propText) {
      animateText();
    }

    return () => {
      loopingRef.current = false;
      if (typeSpeedDelayRef.current) {
        typeSpeedDelayRef.current.cancel();
      }
    };
  }, [propText, animateText]); // Re-run when text changes

  return (
    <div className={'dialog-box-wrap'}>
      <div style={{ ...textStyle }} className="dialog-box-text">
        <p>{text}</p>
        <div
          className={`dialog-box-pointer ${blink && 'animate-pointer'} ${hideCursor ? 'hide-pointer' : ''}`}
        ></div>
      </div>
    </div>
  );
};

DialogBox.propTypes = propTypeValidation;

export default React.memo(DialogBox);
