import React, { useState, useRef, useEffect } from 'react';
import DialogBox from '../DialogBox/DialogBox';
import { AvatarProps } from '../../../types';

import './Avatar.css';

import border from '../../../img/border.svg';
import radio_icon from '../../../img/radio.webp';
import controller_icon from '../../../img/controller.webp';
import pc_icon from '../../../img/pc.webp';
import maw_walking from '../../../img/maw/maw_walking.gif';
import maw_waving from '../../../img/maw/maw_waving.gif';
import maw_standing from '../../../img/maw/maw_standing.gif';
import maw_talking from '../../../img/maw/maw_talking.gif';
import maw_poked from '../../../img/maw/maw_poked.gif';
import cat_walking from '../../../img/cat_walking.gif';
import cat_standing from '../../../img/cat_standing.gif';

const Avatar: React.FC<AvatarProps> = ({
  visitor,
  dialogText,
  setDialogText,
  currentAction,
  setCurrentAction,
  onDialogComplete,
  onOpenBrowser,
}) => {
  const maw = useRef<HTMLImageElement>(null);
  const ralph = useRef<HTMLImageElement>(null);
  const atreyu = useRef<HTMLImageElement>(null);
  const actions = useRef<HTMLDivElement>(null);
  const [userEngaged, setUserEngaged] = useState<boolean>(false);
  const [mawState, setMawState] = useState<string>('talking');
  const [focusedActionNumber, setFocusedActionNumber] = useState<number>(0);
  const [pokeCount, setPokeCount] = useState<number>(0);
  const [isMusicPlaying, setIsMusicPlaying] = useState<boolean>(false);

  const renderVisitorAvailableActions = () => {
    if (!userEngaged) {
      return (
        <>
          <button
            id="action-say-hi"
            className="visitor-action"
            onClick={handleAction}
            aria-label="Say hi to start conversation"
          >
            Say Hi
          </button>
          <button
            id="action-say-meow"
            className="visitor-action"
            onClick={handleAction}
            aria-label="Meow to call the cats"
          >
            Meow!
          </button>
          <button
            id="action-goTo-games"
            className="visitor-action"
            onClick={handleAction}
            aria-label="Play games"
          >
            Play Games
          </button>
          <button
            id="action-say-bye"
            className="visitor-action"
            onClick={handleAction}
            aria-label="Leave the website"
          >
            Leave
          </button>
        </>
      );
    } else {
      if (currentAction === 'say-hi') {
        return (
          <>
            <button id="action-chatter" className="visitor-action" onClick={handleAction}>
              I'm doing fine, thanks.
            </button>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
          </>
        );
      }
      if (currentAction === 'say-meow') {
        return (
          <>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-games" className="visitor-action" onClick={handleAction}>
              Let's play some games!
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
          </>
        );
      }
      if (currentAction === 'chatter') {
        return (
          <>
            <button id="action-goTo-joke" className="visitor-action" onClick={handleAction}>
              I wanna hear a joke!
            </button>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Gimme a random fact!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-joke') {
        return (
          <>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Gimme a random fact!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-fact') {
        return (
          <>
            <button id="action-goTo-joke" className="visitor-action" onClick={handleAction}>
              I wanna hear a joke!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-work') {
        return (
          <>
            <button id="action-goTo-portfolio" className="visitor-action" onClick={handleAction}>
              Can I see your portfolio?
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Tell me something interesting!
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-about') {
        return (
          <>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Tell me something interesting!
            </button>
            <button id="action-goTo-joke" className="visitor-action" onClick={handleAction}>
              I wanna hear a joke!
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-contact') {
        return (
          <>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-joke" className="visitor-action" onClick={handleAction}>
              I wanna hear a joke!
            </button>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Tell me something interesting!
            </button>
          </>
        );
      }
      if (currentAction === 'goTo-portfolio') {
        return (
          <>
            <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
              Let's talk about work!
            </button>
            <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
              Tell me more about yourself
            </button>
            <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
              How can I contact you?
            </button>
            <button id="action-goTo-fact" className="visitor-action" onClick={handleAction}>
              Tell me something interesting!
            </button>
          </>
        );
      }
      // Default case for home or after navigation - show main options
      return (
        <>
          <button id="action-goTo-about" className="visitor-action" onClick={handleAction}>
            Tell me more about yourself
          </button>
          <button id="action-goTo-work" className="visitor-action" onClick={handleAction}>
            Let's talk about work!
          </button>
          <button id="action-goTo-portfolio" className="visitor-action" onClick={handleAction}>
            Show me your portfolio!
          </button>
          <button id="action-goTo-contact" className="visitor-action" onClick={handleAction}>
            How can I contact you?
          </button>
        </>
      );
    }
  };

  const handleAction = (e: React.MouseEvent<HTMLButtonElement>) => {
    const clickSound = document.querySelector('#clickSound') as HTMLAudioElement | null;
    if (clickSound) {
      clickSound.volume = 0.4;
      clickSound.play();
    }
    switch ((e.target as HTMLButtonElement).id) {
      case 'action-say-hi':
        setCurrentAction('say-hi');
        setUserEngaged(true);
        setDialogText(
          `How are you doing today${visitor && ', ' + visitor}? Can I help you with anything? :)`
        );
        break;
      case 'action-say-bye':
        setCurrentAction('say-bye');
        setDialogText('Well... Goodbye!');
        setTimeout(() => {
          window.location.replace(document.referrer);
        }, 2000);
        break;
      case 'action-say-meow':
        setCurrentAction('say-meow');
        setUserEngaged(true);
        setDialogText('So, you like cats? These are Ralph and Atreyu');
        callCats();
        break;
      case 'action-chatter':
        setCurrentAction('chatter');
        setDialogText("I'm really glad you're doing fine. :)");
        break;
      case 'action-goTo-fact':
        setCurrentAction('goTo-fact');
        break;
      case 'action-goTo-joke':
        setCurrentAction('goTo-joke');
        setDialogText('Hmmmmm... let me see...');
        break;
      case 'action-goTo-about':
        setCurrentAction('goTo-about');
        setDialogText('Sure, you can read more about me in the browser window! :)');
        break;
      case 'action-goTo-work':
        setCurrentAction('goTo-work');
        setDialogText('Sure, take a look at the browser window.');
        break;
      case 'action-goTo-contact':
        setCurrentAction('goTo-contact');
        setDialogText(
          'You can reach me through any of the links at the top. Or you can also drop me a line in the contact form in the browser window!'
        );
        break;
      case 'action-goTo-portfolio':
        setCurrentAction('goTo-portfolio');
        setDialogText("These are some of the projects I've worked on recently.");
        break;
      case 'action-goTo-games':
        setCurrentAction('goTo-games');
        setDialogText("Awesome! Let's play some games! You can find them in the navigation bar.");
        break;
      default:
        return;
    }
  };

  const renderDialogBoxText = (dialogText: string) => {
    const typingSound = document.querySelector('#typingSound') as HTMLAudioElement | null;
    if (typingSound) typingSound.volume = 0.4;
    return <DialogBox text={dialogText} onComplete={onDialogComplete} />;
  };

  useEffect(() => {
    const makeJoke = async () => {
      try {
        if (Math.floor(Math.random() * 2)) {
          // Use the new Chuck Norris API endpoint that supports CORS
          const res = await fetch('https://api.chucknorris.io/jokes/random');
          const data = await res.json();
          setDialogText(data.value);
        } else {
          // Use dad jokes API
          const res = await fetch('https://icanhazdadjoke.com/', {
            headers: {
              Accept: 'application/json',
              'User-Agent':
                'MAWMAWMAW (https://github.com/mawmawmaw/mawmawmaw.github.io) https://mawmawmaw.github.io/',
            },
          });
          const data = await res.json();
          setDialogText(data.joke);
        }
      } catch (error) {
        console.error('Error fetching joke:', error);
        // Fallback to a simple joke if API fails
        const fallbackJokes = [
          "Why don't scientists trust atoms? Because they make up everything!",
          'Why did the scarecrow win an award? He was outstanding in his field!',
          "Why don't eggs tell jokes? They'd crack up!",
          'What do you call a fake noodle? An impasta!',
          'Why did the coffee file a police report? It got mugged!',
        ];
        const randomJoke = fallbackJokes[Math.floor(Math.random() * fallbackJokes.length)];
        setDialogText(randomJoke);
      }
    };
    const getFact = async () => {
      try {
        const res = await fetch('https://uselessfacts.jsph.pl/random.json?language=en');
        const data = await res.json();
        setDialogText(data.text);
      } catch (error) {
        // Fallback to a simple fact if API fails
        const fallbackFacts = [
          'Honey never spoils. Archaeologists have found pots of honey in ancient Egyptian tombs that are over 3,000 years old!',
          "A group of flamingos is called a 'flamboyance'.",
          "Bananas are berries, but strawberries aren't!",
          'The shortest war in history lasted only 38 minutes.',
          'Octopuses have three hearts and blue blood!',
        ];
        const randomFact = fallbackFacts[Math.floor(Math.random() * fallbackFacts.length)];
        setDialogText(randomFact);
      }
    };
    if (currentAction === 'goTo-joke') {
      makeJoke();
    }
    if (currentAction === 'goTo-fact') {
      getFact();
    }
  }, [currentAction, setDialogText]);

  useEffect(() => {
    setTimeout(() => {
      if (mawState === 'talking' && !userEngaged && maw.current) {
        maw.current.src = maw_waving;
        setMawState('waving');
      }
      if (mawState === 'waving' && userEngaged && maw.current) {
        if (maw.current) maw.current.src = maw_standing;
        setMawState('standing');
      }
    }, 1000);
  }, [mawState, userEngaged]);

  useEffect(() => {
    var inactivityTime = () => {
      var time;
      const logout = () => {
        window.location.replace(document.referrer);
      };
      const resetTimer = () => {
        clearTimeout(time);
        time = setTimeout(logout, 300000);
      };
      window.onload = resetTimer;
      document.onmousemove = resetTimer;
      document.onkeypress = resetTimer;
    };
    inactivityTime();
  }, []);

  const pokeMaw = (): void => {
    if (pokeCount <= 4) {
      const pokeSound = document.querySelector('#pokeSound') as HTMLAudioElement | null;
      if (pokeSound) {
        pokeSound.volume = 0.1;
        pokeSound.play();
      }
      if (maw.current) maw.current.src = maw_poked;
      setDialogText(`Please don't poke me${visitor && ', ' + visitor}!`);
      setPokeCount(pokeCount + 1);
      setTimeout(() => {
        if (maw.current) maw.current.src = maw_standing;
        setMawState('standing');
      }, 1000);
    } else {
      setDialogText("Ok, this is not fun, I'm leaving.");
      const bgMusic = document.querySelector('#bgMusic') as HTMLAudioElement | null;
      const lostSound = document.querySelector('#lostSound') as HTMLAudioElement | null;
      if (bgMusic) {
        bgMusic.pause();
        setIsMusicPlaying(false);
      }
      if (lostSound) {
        lostSound.volume = 0.1;
        lostSound.play();
      }
      const actionsWrapper = document.querySelector('#actions-wrapper') as HTMLElement | null;
      const rightSideWrapper = document.querySelector('.right-side-wrapper') as HTMLElement | null;
      if (actionsWrapper) actionsWrapper.style.display = 'none';
      if (rightSideWrapper) rightSideWrapper.style.display = 'none';
      setTimeout(() => {
        const dialogBoxContainer = document.querySelector(
          '#dialog-box-container'
        ) as HTMLElement | null;
        const avatarShadow = document.querySelector('.avatar-shadow') as HTMLElement | null;
        if (dialogBoxContainer) dialogBoxContainer.style.display = 'none';
        if (avatarShadow) avatarShadow.style.display = 'none';
        if (maw.current) {
          maw.current.src = maw_walking;
          maw.current.animate(
            [{ transform: 'translateX(0px)' }, { transform: 'translateX(3000px)' }],
            {
              duration: 5000,
            }
          );
        }
      }, 2000);
      setTimeout(() => {
        window.location.replace(document.referrer);
      }, 5000);
    }
  };

  const callCats = (): void => {
    if (ralph.current) {
      ralph.current.animate(
        [{ transform: 'translateX(600px)' }, { transform: 'translateX(10px)' }],
        {
          duration: 2000,
          fill: 'forwards',
        }
      );
    }
    if (atreyu.current) {
      atreyu.current.animate(
        [{ transform: 'translateX(800px)' }, { transform: 'translateX(-40px)' }],
        {
          duration: 2200,
          fill: 'forwards',
        }
      );
    }
    setTimeout(() => {
      if (ralph.current) ralph.current.src = cat_standing;
      if (atreyu.current) atreyu.current.src = cat_standing;
    }, 2200);
  };

  const handleRadioClick = () => {
    const bgMusic = document.querySelector('#bgMusic') as HTMLAudioElement | null;
    if (bgMusic) {
      if (isMusicPlaying) {
        bgMusic.pause();
        setIsMusicPlaying(false);
      } else {
        bgMusic.play().catch(() => {});
        setIsMusicPlaying(true);
      }
    }
  };

  useEffect(() => {
    const bgMusic = document.querySelector('#bgMusic') as HTMLAudioElement | null;

    if (bgMusic) {
      // Check initial state
      if (!bgMusic.paused) {
        setIsMusicPlaying(true);
      }

      // Add event listeners for play/pause events
      const handlePlay = () => setIsMusicPlaying(true);
      const handlePause = () => setIsMusicPlaying(false);

      bgMusic.addEventListener('play', handlePlay);
      bgMusic.addEventListener('pause', handlePause);

      // Clean up event listeners
      return () => {
        bgMusic.removeEventListener('play', handlePlay);
        bgMusic.removeEventListener('pause', handlePause);
      };
    }
  }, []);

  useEffect(() => {
    const handleKeyboard = (e: KeyboardEvent) => {
      if (e.keyCode === 38) {
        if (focusedActionNumber > 0) setFocusedActionNumber(focusedActionNumber - 1);
      }
      if (e.keyCode === 40) {
        if (actions.current && focusedActionNumber < actions.current.children.length - 1)
          setFocusedActionNumber(focusedActionNumber + 1);
      }
    };
    document.addEventListener('keydown', handleKeyboard);

    return () => {
      document.removeEventListener('keydown', handleKeyboard);
    };
  }, [focusedActionNumber]);

  useEffect(() => {
    if (actions.current && actions.current.children[focusedActionNumber]) {
      (actions.current.children[focusedActionNumber] as HTMLElement).focus();
    }
  }, [focusedActionNumber]);

  return (
    <>
      <div id="avatar-container">
        <img
          ref={maw}
          id="avatar"
          src={maw_talking}
          alt="Maw avatar - click to interact"
          onClick={pokeMaw}
          role="button"
          tabIndex={0}
        />
        <div id="cats">
          <img ref={ralph} id="ralph" src={cat_walking} alt="Ralph" />
          <img ref={atreyu} id="atreyu" src={cat_walking} alt="Atreyu" />
        </div>
        <div
          id="player-controls"
          className={isMusicPlaying ? 'playing' : 'paused'}
          onClick={handleRadioClick}
          role="button"
          tabIndex={0}
          aria-label="Toggle music"
          onKeyPress={(e) => e.key === 'Enter' && handleRadioClick()}
        >
          <img
            src={radio_icon}
            alt="Radio - click to toggle music"
            title={isMusicPlaying ? 'Click to pause music' : 'Click to play music'}
          />
        </div>
        <div
          id="game-controller"
          onClick={() => {
            const clickSound = document.querySelector('#clickSound') as HTMLAudioElement | null;
            if (clickSound) {
              clickSound.volume = 0.4;
              clickSound.play();
            }
            setCurrentAction('goTo-games');
            setUserEngaged(true);
            setDialogText("Awesome! Let's play some games! You can find them in the navigation bar.");
          }}
          role="button"
          tabIndex={0}
          aria-label="Go to Games"
          title="Play games!"
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              setCurrentAction('goTo-games');
              setUserEngaged(true);
              setDialogText("Awesome! Let's play some games! You can find them in the navigation bar.");
            }
          }}
        >
          <img
            src={controller_icon}
            alt="Game controller - click to play games"
          />
        </div>
        <div
          id="pc-icon"
          onClick={() => {
            const clickSound = document.querySelector('#clickSound') as HTMLAudioElement | null;
            if (clickSound) {
              clickSound.volume = 0.4;
              clickSound.play();
            }
            onOpenBrowser?.();
          }}
          role="button"
          tabIndex={0}
          aria-label="Open browser"
          title="Open browser"
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              onOpenBrowser?.();
            }
          }}
        >
          <img src={pc_icon} alt="PC - click to open browser" />
        </div>
        <div className="avatar-shadow"></div>
      </div>
      <div id="dialog-box-container">
        <div id="dialog-box" style={{ borderImageSource: `url(${border})` }}>
          {renderDialogBoxText(dialogText)}
        </div>
      </div>
      <div id="actions-wrapper" style={{ borderImageSource: `url(${border})` }}>
        <div id="action-suggestion">
          <p>Choose an option</p>
        </div>
        <div id="actions" ref={actions}>
          {renderVisitorAvailableActions()}
        </div>
      </div>
    </>
  );
};

export default Avatar;
