export class delay {
  time: number;
  timeout: NodeJS.Timeout | null;
  close: ((reason?: any) => void) | null;

  constructor(time: number) {
    this.time = time;
    this.timeout = null;
    this.close = null;
  }

  getPromise(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      this.close = reject;
      this.timeout = setTimeout(() => {
        resolve();
      }, this.time);
    });
  }

  cancel() {
    this.timeout && clearTimeout(this.timeout);
    this.close && this.close('unmounted');
    return { isCanceled: true };
  }
}

export class makeCancelable {
  promise: () => Promise<any>;
  close: (() => void) | null;

  constructor(promise: () => Promise<any>) {
    this.promise = promise;
    this.close = null;
  }

  getPromise(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.close = () => reject('rejected promise');
      this.promise()
        .then((val: any) => resolve(val))
        .catch((err: any) => reject(err));
    });
  }

  cancel() {
    this.close && this.close();
    //throw new Error("error");
    return { isCanceled: true };
  }
}

export const propTypeValidation = {
  typeSpeed: (props: any, propName: string) => {
    if (props[propName] && typeof props[propName] != 'number')
      return new Error(`Invalid ${propName} supplied to react-typeWriter component.`);
  },
  startDelay: (props: any, propName: string) => {
    if (props[propName] && typeof props[propName] != 'number')
      return new Error(`Invalid ${propName} supplied to react-typeWriter component.`);
  },
  text: (props: any, propName: string) => {
    if (!props['multiText'] && typeof props[propName] != 'string')
      return new Error(`Invalid ${propName} supplied to react-typeWriter component!`);
  },
};
